import React from "react";

import img from "./images/pic.jpg";
import "./App.css";
import { Link } from "react-router-dom";

function User_details() {
  const items = { ...localStorage };

  return (
    <>
      <div className="contents">
        <div className="left_img" style={{ objectFit: "cover" }}>
          <img src={img} height="100%" />
        </div>
        <div className="right_wrapper">
          <div className="right_content">
            <h3>List Of Users!</h3>

            <ul className="list">
              {Object.keys(items).map((item) => {
                const value = JSON.parse(items[item]).email;
                return (
                  <li key={item}>
                    <span>Name = {item}</span> , Email = {value}
                  </li>
                );
              })}
            </ul>
          </div>
        </div>
      </div>

      <p>
        Note : Please click here on the link for going the login page and
        <Link to="/">REGISTRATION PAGE</Link>
        click here for login page
        <Link to="/login">LOGIN</Link>
      </p>
    </>
  );
}

export default User_details;
